<template>
  <v-data-table :headers="headers" :items="usersData" hide-default-footer class="elevation-1">
    <template v-slot:top>
      <v-toolbar flat color="white">
        <v-toolbar-title></v-toolbar-title>
        <v-divider class="mx-4" inset vertical></v-divider>
        <v-spacer></v-spacer>
        <v-dialog v-model="dialog" max-width="800px">
          <template v-slot:activator="{ on, attrs }">
            <v-btn color="primary" dark class="mb-2" v-bind="attrs" v-on="on"> </v-btn>
          </template>
          <v-card id="myform" class="center wrapper">
            <v-card-text>
              <v-container>
                <v-row>
                  <v-col cols="12" sm="6" md="4">
                    <v-text-field
                      v-model="editedUser.userName"
                      :rules="[rules.required]"
                      label=" "
                      autocomplete="off"
                    > </v-text-field>
                  </v-col>
                  <v-col cols="12" sm="6" md="4">
                    <v-text-field
                      v-model="editedUser.firstName"
                      :rules="[rules.required]"
                      label=" "
                      autocomplete="off"
                    > </v-text-field>
                  </v-col>
                  <v-col cols="12" sm="6" md="4">
                    <v-text-field
                      v-model="editedUser.lastName"
                      :rules="[rules.required]"
                      label=" "
                      autocomplete="off"
                    > </v-text-field>
                  </v-col>
                </v-row>
                <v-row>
                  <v-col cols="12" sm="6" md="4">
                    <v-text-field
                      v-model="editedUser.password"
                      :append-icon="show1 ? 'mdi-eye' : 'mdi-eye-off'"
                      :rules="[rules.required, rules.min]"
                      :type="show1 ? 'text' : 'password'"
                      name="input-10-1"
                      label=""
                      hint=" 6 "
                      @click:append="show1 = !show1"
                    ></v-text-field>
                  </v-col>
                </v-row>
                <v-row>
                  <v-col cols="12" sm="6" md="4">
                    <v-text-field
                      v-model="confirmPassword"
                      :append-icon="show2 ? 'mdi-eye' : 'mdi-eye-off'"
                      :rules="[rules.required, rules.min, rules.passwordMatch]"
                      :type="show2 ? 'text' : 'password'"
                      :disabled="editedUser.password == ''"
                      name="input-10-1"
                      label="  "
                      hint=" 6 "
                      @click:append="show2 = !show2"
                    ></v-text-field>
                  </v-col>
                </v-row>
                <v-row>
                  <v-col cols="12" md="4">
                    <v-select
                      id="autocomlete"
                      label=" "
                      :items="mossadotList"
                      v-model="editedUser.mossadId"
                      item-text="mossadName"
                      item-value="mossadId"
                      autocomplete
                    ></v-select>
                  </v-col>
                </v-row>
                <v-btn color="info" @click="saveUser()"> </v-btn>
                <v-btn color="red" @click="close()"></v-btn>
              </v-container>
            </v-card-text>
          </v-card>
        </v-dialog>
      </v-toolbar>
    </template>
    <!-- <template v-slot:[x60;item.actions&#x60;]="{ item }"> -->
    <!-- <template v-slot:[`item.actions`]="{ item }"> -->
        <template v-slot:item.mossadId="{ item }">
            {{formatMossadId(item.mossadId)}}
        </template>
    <template v-slot:item.actions="{ item }">
      <v-icon small class="mr-2" @click="editItem(item)">mdi-pencil</v-icon>
      <v-icon small @click="deleteItem(item)">mdi-delete</v-icon>
    </template>
    <template v-slot:no-data>
      <v-btn color="primary" @click="initialize">Reset</v-btn>
    </template>
  </v-data-table>
</template>

<script>
import axios from "axios";
export default {
  name: "test",
  data: () => ({
    dialog: false,
    headers: [
      { text: " ", value: "firstName" },
      { text: " ", value: "lastName" },
      { text: " ", value: "userName" },
      { text: "", value: "password" },
      { text: "", value: "mossadId" },
      { text: "", value: "actions", sortable: false },
    ],
    usersData: [],
    mossadotList: [],
    editedIndex: -1,
    editedUser: {
      firstname: "",
      lastName: "",
      userName: "",
      password: "",
      mossadId: "",
    },
    defaultUser: {},
    rules: {
      required: (value) => !!value || " ",
      min: (v) => v.length >= 6 || " 6 ",
      passwordMatch: (val) =>
        val === this.editedUser.password || "   ",
    },
    show1: false,
    show2: false,
    confirmPassword: "",
  }),

  computed: {
     
  },

  watch: {
    dialog(val) {
      val || this.close();
    },
  },
  created() {
    this.initialize();
    this.getMossadot();
    this.getAllUsers();
  },
  methods: {
       formatMossadId(mossadId){
          // eslint-disable-next-line no-debugger
          debugger
          return this.mossadotList.find(el => el.mossadId == mossadId).mossadName
      },
    initialize() {
      this.defaultUser = {
        firstname: "",
        lastName: "",
        userName: "",
        password: "",
        mossadId: "",
      };
    },
    getMossadot() {
      axios
        .get("mossadot/all")
        .then((response) => {
          this.mossadotList = response.data;
          this.mossadotList.push({ mossadName: "", mossadId: 999 });
        })
        .catch((e) => console.log(e));
    },
    getAllUsers() {
      axios
        .get("/auth/all")
        .then((response) => (this.usersData = response.data))
        .catch((error) => console.log(error));
    },
    editItem(item) {
      this.editedIndex = this.usersData.indexOf(item);
      this.editedUser = Object.assign({}, item);
      this.dialog = true;
    },
    deleteItem(item) {
      const index = this.usersData.indexOf(item);
      if (confirm("      ?")) {
        this.usersData.splice(index, 1);
        this.deleteuser(item);
      }
    },
    deleteuser(user) {
      axios
        .delete("/auth/delete", {
          params: {
            username: user.userName,
          },
        })
        .then(() => {
          alert("  ");
        })
        .catch("    ");
    },
    close() {
      this.dialog = false;
      this.$nextTick(() => {
        this.editedUser = Object.assign({}, this.defaultUser);
        this.editedIndex = -1;
      });
    },
    // saveItem() {
    //   // eslint-disable-next-line no-debugger
    //   debugger;
    //   // save or update
    //   if (this.editedIndex > -1) {
    //     if (this.saveUser(false) == true) {
    //       Object.assign(this.usersData[this.editedIndex], this.editedUser);
    //       this.close();
    //     }
    //   } else {
    //     if (this.saveUser(true) == true) {
    //       this.usersData.push(this.editedUser);
    //       this.close();
    //     }
    //   }
    // },
    saveUser() {
      let url = "/auth/save";
      if (this.editedIndex > -1) {
        url = "/auth/update";
      }
      if (
        this.editedUser.userName == "" ||
        this.editedUser.password == "" ||
        this.editedUser.mossadId == "" ||
        this.editedUser.firstName == "" ||
        this.editedUser.lastName == ""
      ) {
        alert("    ");
        return;
      }
      if (this.editedUser.password != this.confirmPassword) {
        alert("  ");
        return;
      }
      if (this.editedUser.password.length < 6) {
        alert("    6 ");
        return;
      }
      axios
        .post(url, {
          userName: this.editedUser.userName,
          password: this.editedUser.password,
          mossadId: this.editedUser.mossadId,
          firstName: this.editedUser.firstName,
          lastName: this.editedUser.lastName,
        })
        .then(() => {
          alert("  ");
          if (this.editedIndex > -1) {
            Object.assign(this.usersData[this.editedIndex], this.editedUser);
          } else {
            this.usersData.push(this.editedUser);
          }
          this.confirmPassword = "";
          this.close();
        })
        .catch((error) => {
          this.$store.dispatch("displayErrorMessage", {
            error,
          });
        });
    },
  },
};
</script>

<style scoped>
::v-deep .v-input__slot {
  direction: ltr;
}
::v-deep .v-input input {
  direction: ltr;
}

#autocomlete {
  max-width: 150px;
  max-height: 25px;
  padding-top: 0;
}
</style>